import React, { useEffect, useState } from 'react';
import { navigate } from 'gatsby';
import { fetchSupplyLists, fetchSupplyListDetails } from '../tables/tables';
import Layout from './Layout';

interface ListMetrics {
  expenses: number;
  revenue: number;
  profit: number;
  margin: number;
}

export default function AllLists() {
  const [lists, setLists] = useState<string[]>([]);
  const [metrics, setMetrics] = useState<Record<string, ListMetrics>>({});
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadData = async () => {
      const listNames = await fetchSupplyLists();
      setLists(listNames);

      const metricsData: Record<string, ListMetrics> = {};

      await Promise.all(
        listNames.map(async name => {
          const details = await fetchSupplyListDetails(name);

          const items = details.items || [];

          let expenses = 0;
          let revenue = 0;
          let profit = 0;

          items.forEach((item: any) => {
            const costEa = item.bulk / item.carton;
            expenses += costEa * item.qty;
            revenue += item.sell * item.qty;
            profit += (item.sell - costEa) * item.qty;
          });

          const margin = revenue > 0 ? (profit / revenue) * 100 : 0;

          metricsData[name] = {
            expenses,
            revenue,
            profit,
            margin,
          };
        })
      );

      setMetrics(metricsData);
      setLoading(false);
    };

    loadData();
  }, []);

  const totalProfit = Object.values(metrics).reduce((acc, m) => acc + m.profit, 0);
  const averageProfit = lists.length > 0 ? totalProfit / lists.length : 0;

  const totalMargin = Object.values(metrics).reduce((acc, m) => acc + m.margin, 0);
  const averageMargin = lists.length > 0 ? totalMargin / lists.length : 0;

  return (
    <Layout>
      <div style={{ padding: 20, fontFamily: 'Arial' }}>
        <h2>All Supply Lists</h2>
        {loading ? (
          <p>Loading...</p>
        ) : (
          <table border={1} cellPadding={6} cellSpacing={0}>
            <thead>
              <tr>
                <th>#</th>
                <th>List Name</th>
                <th>Expenses</th>
                <th>Revenue</th>
                <th>Profit</th>
                <th>Current Margin (%)</th>
              </tr>
            </thead>
            <tbody>
              {lists.map((name, idx) => {
                const data = metrics[name];
                return (
                  <tr key={name}>
                    <td>{idx + 1}</td>
                    <td
                      style={{ cursor: 'pointer', color: 'black' }}
                      onClick={() => navigate(`/lists/${encodeURIComponent(name)}`)}
                    >
                      {name}
                    </td>
                    <td>${data.expenses.toFixed(2)}</td>
                    <td>${data.revenue.toFixed(2)}</td>
                    <td>${data.profit.toFixed(2)}</td>
                    <td>{data.margin.toFixed(2)}%</td>
                  </tr>
                );
              })}
              <tr>
                <td colSpan={4}><strong>Average Profit</strong></td>
                <td colSpan={2}><strong>${averageProfit.toFixed(2)}</strong></td>
              </tr>
              <tr>
                <td colSpan={4}><strong>Average Margin</strong></td>
                <td colSpan={2}><strong>{averageMargin.toFixed(2)}%</strong></td>
              </tr>
            </tbody>
          </table>
        )}
      </div>
    </Layout>
  );
}
